body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body {
  background-color: #ffe8a6;
  overflow: hidden;
}

a {
  text-decoration: none;
}

.home {
  display: flex;
  justify-content: center;
  height: 100vh;
}

.today-container {
  background-color: #fffbf1;
  align-self: center;
  width: 62vw;
  height: 56vh;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  padding: 4vw;
}

.tagline {
  position: absolute;
  left: 20vw;
  top: 23vh;
  font-family: "Lato", sans-serif;
  font-size: 2.5vh;
  color: #2e61e3;
  font-weight: 300;
  letter-spacing: 0.5vh;
  margin: 3vh;
  z-index: 3;
}

.tagline a:link,
.tagline a:visited,
.signature a:link,
.signature a:visited {
  color: #fc6d6d;
}

.tagline a:link,
a:visited {
  font-weight: 400;
}

.tagline a:hover,
.signature a:hover {
  color: #eb5555;
}

.today {
  position: absolute;
  left: 12vw;
  top: 27vh;
  font-family: "DM Serif Display", serif;
  font-size: 13vh;
  color: #5498e8;
  z-index: 2;
}

.insp-header {
  position: fixed;
  padding: 0.5vh;
  padding-right: 1vw;
  padding-left: 1vw;
  background-color: rgb(230, 230, 247);
  border: 1px solid #2e61e3;
  z-index: 2;
  height: 2vh;
  border-radius: 100vh;
  font-size: 1.3vh;
  font-family: "Lato", "serif";
  color: #2e61e3;
  letter-spacing: 0.1vw;
}

.insp-1 .insp-header {
  top: 48vh;
  right: 6vw;
}

.insp-2 .insp-header {
  bottom: 28vh;
  left: 28vw;
}

.insp-img {
  position: relative;
  height: 100%;
  z-index: 1;
  width: auto;
}

.insp-1 {
  position: absolute;
  top: 19vh;
  right: 5vw;
  height: 30vh;
  width: 30vw;
  max-width: 30vw;
  max-height: 30vh;
  overflow: hidden;
  justify-content: center;
  display: flex;
}

.insp-2 {
  position: absolute;
  left: 10vw;
  bottom: 5vh;
  height: 25vh;
  width: 35vw;
  max-width: 30vw;
  max-height: 30vh;
  overflow: hidden;
  justify-content: center;
  display: flex;
}

.daily-tagline {
  position: absolute;
  top: 4vh;
  padding-left: 4vw;
  font-style: italic;
  font-size: 0.8vw;
  font-weight: 300;
  color: #5498e8;
}

.daily-prompt,
.weekly-prompt {
  position: relative;
  height: 30vh;
  width: 20vw;
  margin: 1vh;
  padding: 1vw;
}

.prompt-header {
  font-family: "Lato", sans-serif;
  font-size: 2vh;
  color: #2e61e3;
  font-weight: 400;
  letter-spacing: 0.5vh;
  padding-left: 2.5vw;
  text-transform: uppercase;
  padding-bottom: 2.5vh;
}

.prompt-text {
  position: relative;
  font-family: "DM Serif Display", serif;
  font-size: 5vh;
  color: #2e61e3;
  font-weight: 300;
  letter-spacing: 0vw;
  margin-top: -3.5vh;
  padding-left: -5vh;
  font-style: italic;
}

.palette {
  font-family: "Lato", sans-serif;
  font-size: 2.5vh;
  color: #2e61e3;
  font-weight: 300;
  letter-spacing: 0.5vh;
  padding-left: 2.5vw;
  text-transform: lowercase;
}

.circle-1 {
  width: 100%;
  padding: 2vw;
  bottom: 18vh;
  left: 55vw;
  position: absolute;
}
.circle-2 {
  width: 100%;
  padding: 2vw;
  top: 76vh;
  left: 32vw;
  position: absolute;
  z-index: 3;
}

ul {
  margin: 0;
  margin-left: -1vw;
}

.signature {
  position: absolute;
  right: 18vw;
  bottom: 12.5vh;
  font-size: 0.8vw;
  font-weight: 400;
  color: #fc6d6d;
  z-index: 4;
}

.signature a {
  font-weight: 600;
}

/* Animations */

.home {
  -webkit-animation: fadein 2s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
  animation: fadein 2s;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Firefox < 16 */

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Internet Explorer */

/* Opera < 12.1 */

.nav {
  position: absolute;
  top: 12vh;
}

.nav-link {
  text-transform: uppercase;
  text-decoration: none;
  font-family: "Lato", sans-serif;
  font-size: 0.75vw;
  color: #fc6d6d;
  font-weight: 600;
  letter-spacing: 0.5vh;
  margin: 3vh;
}

.nav-link:hover {
  color: #eb5555;
}

