  color: #5498e8;
}

.body-text {
  font-family: "Lato", sans-serif;
  font-size: 1.5vh;
  color: #2e61e3;
  font-weight: 400;
  letter-spacing: 0.2vh;
  padding-left: 2vw;
  padding-right: 2vw;
  padding-top: 2vh;
  padding-bottom: 2vh;
}

.body-text a:link,
.body-text a:visited {
  color: #fc6d6d;
}

.body-text a:hover {
  color: #eb5555;
}

.numbers {
  font-family: "DM Serif Display", serif;
  font-size: 5vh;
  color: #fc6d6d;
  width: 5vh;
  font-style: italic;
}

.guideline {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.about-text {
  width: 36vw;
}

